$big-content-width: 800px;
$medium-content-width: 400px;

@-moz-keyframes showUp {
  0% { opacity: 0; }
  50% { opacity: 0.3; }
  100% { opacity: 0.13; }
}
@-moz-keyframes showUpLogo {
  0% { opacity: 0; }
  25% { opacity: 0.6; }
  75% { opacity: 0.3; }
  100% { opacity: 1; }
}
@-webkit-keyframes showUp {
  0% { opacity: 0; }
  50% { opacity: 0.3; }
  100% { opacity: 0.13; }
}
@-webkit-keyframes showUpLogo {
  0% { opacity: 0; }
  25% { opacity: 0.6; }
  75% { opacity: 0.3; }
  100% { opacity: 1; }
}
@keyframes showUp {
  0% { opacity: 0; }
  50% { opacity: 0.3; }
  100% { opacity: 0.13; }
}
@keyframes showUpLogo {
  0% { opacity: 0; }
  25% { opacity: 0.6; }
  75% { opacity: 0.3; }
  100% { opacity: 1; }
}

body {
  padding: 0;
  background-color: white;
  color: #282b34;
  font-family: sans-serif;
  font-size: 1rem;
  text-align: center;
}

h2 {
  margin: 10px 0;
  font-size: 24px;
  color: #282b34;

  @media screen and (min-width: $medium-content-width) {
    font-size: 30px;
  }
}

img {
  display: block;
  margin: 0 auto;
}

.code-editor-container {
  overflow: auto;

  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

.code-editor {
  float: left;
  background-color: rgba(10, 10, 10, 0.95);
  border: 0;
  color: white;
  font-family: monospace;
  font-size: 1rem;
  text-align: left;
  width: 100%;
  height: 600px;
  padding: 10px;
  box-sizing: border-box;
  overflow: auto;
  resize: none;
  margin-bottom: 10px;

  .CodeMirror {
    height: 575px;
  }

  @media screen and (min-width: $medium-content-width) {
    width: $medium-content-width;
    &:first-of-type {
      border-right: 1px solid white;
    }
  }
}

.code-sample {
  position: relative;
  display: inline-block;
  text-align: center;
  width: 100%;
  overflow-x: scroll;

  @media screen and (min-width: $big-content-width) {
    width: auto;
  }

  &.left {
    text-align: left;
  }

  .CodeMirror {
    padding: 5px;
    height: auto;
  }
}

section {
  margin-bottom: 40px;
}

a {
  color: #282b34;

  &:hover {
    color: #888888;
  }
  &:visited {
    color: #888888;
  }
}

table.emojis-map {
  width: 100%;
  font-weight: bold;

  .keyword {
    font-size: 9px;
  }

  td {
    width: 10%;
    padding-top: 10px;
  }

  @media screen and (min-width: $big-content-width) {
    .keyword {
      font-size: 1em;
    }
  }
}

.background-emojis {
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  display: table;
}

.bg-row {
  display: table-row;
}

.bg-elem {
  animation: showUp .8s ease-in-out;
  opacity: 0.13;
  display: table-cell;
  font-size: 1.7vw;
  line-height: 3.3vw;

  &.logo {
    animation: showUpLogo .8s ease-in-out;
    opacity: 1;
    font-size: 3vw;
    line-height: 3vw;
  }

  &.delay-1 {
    animation-delay: .05s;
  }
  &.delay-2 {
    animation-delay: .15s;
  }
  &.delay-3 {
    animation-delay: .2s;
  }
  &.delay-4 {
    animation-delay: .25s;
  }

  @media screen and (min-width: $big-content-width) {
    font-size: 3vw;
    line-height: 3vw;
  }
}

.content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100vw;
}

.content {
  max-width: 800px;
  margin: 0 auto;
  padding: 50vw 10px 0 10px;
  text-align: center;

  @media screen and (min-width: $medium-content-width) {
    width: $medium-content-width;
    padding-top: 49vw;
  }

  @media screen and (min-width: $big-content-width) {
    width: $big-content-width;
    padding-top: 44vw;
  }
}

.instructions {
  box-shadow: 0px 0px 30px 20px rgba(255, 255, 255, 0.9);
  background-color: rgba(255, 255, 255, 0.9);

  p {
    text-align: left;
  }

  @media screen and (min-width: $big-content-width) {
    p {
      text-align: center;
    }
  }
}

.disclamer-note {
  font-size: 14px;
  color: #aaaaaa;
  padding-top: 20px;
  border-top: 1px solid #dddddd;
}

